import React, { Component } from "react";
import {
    Row, Col
} from "reactstrap";
import "./BotMap.css";

import samplebot from "../assets/images/samplebot3.png";
import Header from "../components/header/Header";

class BotMap extends Component {
    render() {
        return (
            <React.Fragment>
                <Header></Header>
                <div className="body-height main-bg-color">
                    <Row className=" m-0">
                        <Col xl="6"></Col>
                        <Col xl="6" lg="12">
                            <center>
                                <span className="botmap-title-txt">BotMap</span>
                            </center>
                        </Col>
                    </Row>
                    <Row className="main-bg-color m-0 py-1">
                        <Col xl="5" lg="12">
                            <center>
                                <span>
                                    <img src={samplebot} className="botmap-bot" alt="..." />
                                </span>
                            </center>
                        </Col>
                        <Col xl="7" lg="12" className="bot-map-content">
                            <div className="w-100">
                                <p className="q-title">
                                    Q1 - Launch
                                </p>
                                <div>
                                    <div className="d-flex">
                                        <div>
                                            <p className="q-text px-1 m-0">&#183;</p>
                                        </div>
                                        <div>
                                            <span className="q-text m-0">
                                                Giveaways and promotion.
                                            </span>
                                        </div>
                                    </div>
                                    <div className="d-flex">
                                        <div>
                                            <p className="q-text px-1 m-0">&#183;</p>
                                        </div>
                                        <div>
                                            <span className="q-text m-0">
                                                Initiate all 2000 Bots.
                                            </span>
                                        </div>
                                    </div>
                                    <div className="d-flex">
                                        <div>
                                            <p className="q-text px-1 m-0">&#183;</p>
                                        </div>
                                        <div>
                                            <span className="q-text m-0">
                                                Reveal rarity of each trait.
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <Row>
                                <Col xl="5" lg="6"></Col>
                                <Col xl="7" lg="6">
                                    <p className="q-title">
                                        Q2 - Bot Exchange
                                    </p>
                                    <div className="d-flex">
                                        <div>
                                            <p className="q-text px-1 m-0">&#183;</p>
                                        </div>
                                        <div>
                                            <span className="q-text m-0">
                                                Release of the Bot Exchange for all Bot Owners to use.
                                            </span>
                                        </div>
                                    </div>
                                    <div className="d-flex">
                                        <div>
                                            <p className="q-text px-1 m-0">&#183;</p>
                                        </div>
                                        <div>
                                            <span className="q-text m-0">
                                                Start staking via CRODEX
                                            </span>
                                        </div>
                                    </div>
                                </Col>
                            </Row>
                            <div className="q3-box">
                                <p className="q-title">
                                    Q3 - Upgrade Vault
                                </p>
                                <div className="d-flex">
                                    <div>
                                        <p className="q-text px-1 m-0">&#183;</p>
                                    </div>
                                    <div>
                                        <span className="q-text m-0">
                                            Staggered release of CPU <br />
                                            Upgrade Chips.
                                        </span>
                                    </div>
                                </div>
                                <div className="d-flex">
                                    <div>
                                        <p className="q-text px-1 m-0">&#183;</p>
                                    </div>
                                    <div>
                                        <span className="q-text m-0">
                                            Initial sneak peak at <br />
                                            BILLIONAIRE BOTS <br />
                                            (Supply=100)
                                        </span>
                                    </div>
                                </div>
                            </div>
                            <Row className="w-100 q4-box">
                                <Col xl="5" lg="6"></Col>
                                <Col xl="7" lg="6">
                                    <p className="q-title">
                                        Q4 - Future
                                    </p>
                                    <div className="d-flex">
                                        <div>
                                            <p className="q-text px-1 m-0">&#183;</p>
                                        </div>
                                        <div>
                                            <span className="q-text m-0">
                                                Look into partnerships with
                                                huge NFT projects and  real
                                                world brands.
                                            </span>
                                        </div>
                                    </div>
                                    <div className="d-flex">
                                        <div>
                                            <p className="q-text px-1 m-0">&#183;</p>
                                        </div>
                                        <div>
                                            <span className="q-text m-0">
                                                Develop the project into new
                                                and bigger  ventures such as
                                                crypto gaming.
                                            </span>
                                        </div>
                                    </div>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                </div>

            </React.Fragment>
        );
    }
}

export default BotMap;
