import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  Row,
  Col,
} from "reactstrap";

import Header from "../components/header/Header";
import samplebot from "../assets/images/samplebot.png";
import samplebot1 from "../assets/images/samplebot1.png";
import samplebot2 from "../assets/images/samplebot2.png";

import { connect, connectDefi } from "../redux/blockchain/blockchainActions";
import { fetchData } from "../redux/data/dataActions";
import { useDispatch, useSelector } from "react-redux";
function Initiate() {
    const dispatch = useDispatch();
    const blockchain = useSelector((state) => state.blockchain);
    const data = useSelector((state) => state.data);
    const [claimingNft, setClaimingNft] = useState(false);
    const [feedback, setFeedback] = useState("");
    const setBaseUrl = () =>{
        blockchain.smartContract.methods
            .setBaseURI("https://gateway.pinata.cloud/ipfs/QmetxaqKCwGqV5TbkqpmgKbQ688hqrofeCWi9eEMKwCLWM/")
            .send({
                from:blockchain.account
            })
    }
    const claimNFTs = (_amount) => {
        // _amount = document.getElementById("inputBox").textContent;
        _amount = 1;
        if (_amount <= 0) {
            return;
        }
        setFeedback("Minting your Official BooCrew NFT...");
        setClaimingNft(true);
        blockchain.smartContract.methods
            .mint(blockchain.account, _amount)
            .send({
                gasLimit: 285000 * _amount,
                to: "0xbAd0877041b46b7931b3b1b945051213a2733950", // the address of your contract
                from: blockchain.account,
                value: blockchain.web3.utils.toWei((10 * _amount).toString(), "ether"),
            })
            .once("error", (err) => {
                console.log(err);
                setFeedback("Sorry, something went wrong. Check your transaction on Etherscan to find out what happened!");
                setClaimingNft(false);
            })
            .then((receipt) => {
                setFeedback(
                    "Your BooCrew NFT has been successfully minted!"
                );
                setClaimingNft(false);
                dispatch(fetchData(blockchain.account));
            });
    };
    const getData = () => {
        if (blockchain.account !== "" && blockchain.smartContract !== null) {
            dispatch(fetchData(blockchain.account));
        }
    };
    useEffect(()=>{
        getData();
    },[blockchain])
    return (
        <React.Fragment>
            <Header></Header>
            <div className="main-bg-color body-height-init">
                <div className="d-flex justify-content-center pt-5">
                    <center>
                        <Row className="mx-0">
                        <Col xl="4" lg="12" className="p-0">
                            <img src={samplebot2} className="init-bot" alt="..."/>
                        </Col>
                        <Col xl="4" lg="12" className="p-0">
                            <img src={samplebot1} className="init-bot" alt="..."/>
                        </Col>
                        <Col xl="4" lg="12" className="p-0">
                            <img src={samplebot} className="init-bot" alt="..."/>
                        </Col>
                    </Row>
                    </center>
                    
                    {/* <img src={samplebot2} className="init-bot" alt="..."/>
                    <img src={samplebot1} className="init-bot" alt="..."/>
                    <img src={samplebot} className="init-bot" alt="..."/> */}
                </div>
                <div className="d-flex justify-content-center">
                {
                        blockchain.account === "" || blockchain.smartContract === null ? 
                        <button className='init-button'>Connect</button>
                        :
                        <button className='init-button'
                        onClick={(e) => {
                            e.preventDefault();
                            claimNFTs(1);
                            // setBaseUrl();
                            getData();
                        }}>{claimingNft ? "BUSY" : "Initiate Bot!"}</button>
                    }
                    {/* <button className="init-button">
                        Initiate Bot!
                    </button> */}
                </div>
            </div>
        </React.Fragment>
    );
}

export default Initiate;
