// constants
import Web3EthContract from "web3-eth-contract";
import { DeFiWeb3Connector } from "deficonnect";
import Web3 from "web3";
import SmartContract from "../../contracts/Doodlenauts.json";
// log
import { fetchData } from "../data/dataActions";

const connectRequest = () => {
  return {
    type: "CONNECTION_REQUEST",
  };
};

const connectSuccess = (payload) => {
  return {
    type: "CONNECTION_SUCCESS",
    payload: payload,
  };
};

const connectFailed = (payload) => {
  return {
    type: "CONNECTION_FAILED",
    payload: payload,
  };
};

const updateAccountRequest = (payload) => {
  return {
    type: "UPDATE_ACCOUNT",
    payload: payload,
  };
};
export const connectDefi = () => {
  console.log("--:");
  const { ethereum } = window;
  return async (dispatch) => {
    dispatch(connectRequest());
    const connector = new DeFiWeb3Connector({
      supportedChainIds: [3],
      rpc: {
        1: "https://ropsten.infura.io/v3/f244f3bcf858457fbf912cfccdc29d06",
      },
      pollingInterval: 15000,
    });
    connector.activate();
    const provider = await connector.getProvider();
    const web3 = new Web3(provider);
    console.log("web3:", web3);
    try {
      const accounts = await ethereum.request({
        method: "eth_requestAccounts",
      });
      const networkId = await ethereum.request({
        method: "net_version",
      });
      // const NetworkData = await SmartContract.networks[networkId];
      if (networkId == 338) {
        // IMPORTANT. ONCE YOUR CONTRACT IS ON THE MAIN NET, SWITCH THIS NUMBER TO 1.
        const SmartContractObj = new Web3EthContract(
          SmartContract,
          "0xbAd0877041b46b7931b3b1b945051213a2733950" // **IMPORTANT** PASTE CONTRACT ADDRESS HERE
        );
        console.log(SmartContractObj);
        dispatch(
          connectSuccess({
            account: accounts[0],
            smartContract: SmartContractObj,
            web3: web3,
          })
        );
        // Add listeners start
        ethereum.on("accountsChanged", (accounts) => {
          dispatch(updateAccount(accounts[0]));
        });
        ethereum.on("chainChanged", () => {
          window.location.reload();
        });
        // Add listeners end
      } else {
        dispatch(connectFailed("Change network to Cronos."));
      }
    } catch (err) {
      dispatch(connectFailed(err));
    }
  };
};
export const connect = () => {
  return async (dispatch) => {
    dispatch(connectRequest());
    const { ethereum } = window;
    const metamaskIsInstalled = ethereum && ethereum.isMetaMask;
    if (metamaskIsInstalled) {
      console.log("---");
      Web3EthContract.setProvider(ethereum);
      let web3 = new Web3(ethereum);
      try {
        const accounts = await ethereum.request({
          method: "eth_requestAccounts",
        });
        const networkId = await ethereum.request({
          method: "net_version",
        });
        // const NetworkData = await SmartContract.networks[networkId];
        if (networkId == 338) {
          // IMPORTANT. ONCE YOUR CONTRACT IS ON THE MAIN NET, SWITCH THIS NUMBER TO 1.
          const SmartContractObj = new Web3EthContract(
            SmartContract,
            "0xbAd0877041b46b7931b3b1b945051213a2733950" // **IMPORTANT** PASTE CONTRACT ADDRESS HERE
          );
          console.log(SmartContractObj);
          dispatch(
            connectSuccess({
              account: accounts[0],
              smartContract: SmartContractObj,
              web3: web3,
            })
          );
          // Add listeners start
          ethereum.on("accountsChanged", (accounts) => {
            dispatch(updateAccount(accounts[0]));
          });
          ethereum.on("chainChanged", () => {
            window.location.reload();
          });
          // Add listeners end
        } else {
          dispatch(connectFailed("Change network to Cronos."));
        }
      } catch (err) {
        dispatch(connectFailed("Something went wrong."));
      }
    } else {
      dispatch(connectFailed("Please install Metamask."));
    }
  };
};

export const updateAccount = (account) => {
  return async (dispatch) => {
    dispatch(updateAccountRequest({ account: account }));
    dispatch(fetchData(account));
  };
};
