import React, { useState, useEffect } from "react";
import Header from "../components/header/Header";
import "./MyBots.css";
import { Modal, ListGroup, Button, Row, Col } from "react-bootstrap";
import axios from "axios";
import { fetchData } from "../redux/data/dataActions";
import { useDispatch, useSelector } from "react-redux";
import Card from "../components/card/card";
function MyBots() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [imageUrl, setImageUrl] = useState([]);
  const [name, setName] = useState("");
  const [image, setImage] = useState("");
  const [props, setProps] = useState([]);

  const getData = async () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
      handleClose();
    }
  };
  useEffect(() => {
    getData();
  }, [blockchain]);
  useEffect(async () => {
    if (data.tokenIds.length > 0) {
      const temp = [];
      for (let i = 0; i < data.tokenIds.length; i++) {
        let url = await blockchain.smartContract.methods
          .tokenURI(data.tokenIds[i])
          .call();
        let tempUrl = await getUrlData(url);
        temp.push(tempUrl);
      }
      setImageUrl(temp);
    }
  }, [data.tokenIds]);

  const getUrlData = async (url) => {
    return new Promise((resolve, reject) => {
      resolve(
        axios.get(url).then(function (result) {
          return result.data;
        })
      );
    });
  };

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = (name) => {
    for (var i = 0; i < imageUrl.length; i++) {
      if (imageUrl[i].name === name) {
        setName(imageUrl[i].name);
        setImage(imageUrl[i].Image);
        var temp = [];
        imageUrl[i].attributes.map((item, index) => {
          temp.push({ cardAttribute: item.trait_type, cardValue: item.value });
        });
        console.log(temp);
        setProps(temp);
      }
    }
    setShow(true);
  };
  return (
    <React.Fragment>
      <Header></Header>
      <div className="my-bot-title  main-bg-color">
        <span className="my-bot-title-txt">My Bots</span>
        {blockchain.account === "" || blockchain.smartContract === null ? (
          <p className="my-bot-title-address">
            You should connect wallet first.
          </p>
        ) : (
          <p className="my-bot-title-address">{blockchain.account}</p>
        )}
        <div className="black-line"></div>
      </div>
      <div className="main-bg-color body-height-mybot my-bot-content">
        <Row className="bot-box mx-0">
          {imageUrl.length > 0 &&
            imageUrl.map((item, key) => (
              <Col
                lg="3" md="6"
                key={key}
                onClick={() => handleShow(item.name)}
                style={{ cursor: "pointer" }}
                className="mb-4"
              >
                <center>
                  <div className="card-bot card" >
                    <center>
                      <img
                        src={
                          "https://gateway.pinata.cloud/ipfs/" +
                          item.Image.split("ipfs://")[1]
                        }
                        className="card-img-bot"
                        alt="..."
                      />
                    </center>
                    <div className="card-body">
                      <p className="card-text-bot">{item.name}</p>
                    </div>
                  </div>
                </center>
              </Col>
            ))}
        </Row>
      </div>
      <Modal
        show={show}
        onHide={handleClose}
        backdrop="static"
        size="lg"
        className="card-modal"
      >
        <Modal.Header closeButton>
          <Modal.Title>{name}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Row>
            <Col md={0} lg={6}>
              <center>
                <img
                  src={
                    "https://gateway.pinata.cloud/ipfs/" +
                    image.split("ipfs://")[1]
                  }
                  className="cronos-bot-big-img"
                  onClick={()=> window.open(
                    "https://gateway.pinata.cloud/ipfs/" + image.split("ipfs://")[1], "_blank")} 

                ></img>
              </center>
            </Col>
            <Col md={12} lg={6}>
              <ListGroup>
                {props.length > 0 &&
                  props.map((item, index) => (
                    <ListGroup.Item
                      className="card-list-item"
                    >
                      <Card
                        cardAttribute={item.cardAttribute}
                        cardValue={item.cardValue}
                      ></Card>
                    </ListGroup.Item>
                  ))}
              </ListGroup>
            </Col>
          </Row>
        </Modal.Body>
      </Modal>
    </React.Fragment>
  );
}

export default MyBots;
